@keyframes modal-window-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.tooltip {
  display: flex;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  animation: modal-window-animation 0.3s linear;
  justify-content: center;
  align-items: center;
}